import request from '@/utils/request'

//查询库存
export function fetchList (data) {
  return request({
    url: `/product/business/stock/findByCondition`,
    method: 'post',
    data: data
  })
}


//出入库操作

export function stockChange (data) {
  return request({
    url: `/product/business/stock/updateStockInOut`,
    method: 'post',
    data: data
  })
}

//出入库明细列表

export function getStockDetails (data) {
  return request({
    url: `/product/business/stockRecord/findByCondition`,
    method: 'post',
    data: data
  })
}

//查看明细详情
export function showDetails (data) {
  return request({
    url: `/product/business/stockRecord/findByDetailCondition`,
    method: 'post',
    data: data
  })
}

