<!--
 * @Description: 商品库存查询
 * @Author: your name
 * @Date: 2019-08-27 19:58:00
 * @LastEditTime: 2019-09-10 16:17:06
 * @LastEditors: Please set LastEditors
 -->
<template>
  <div class="app-container">
    <!-- 筛选条件 -->
    <el-card class="operate-container"
             shadow="never">
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button style="float: right"
                   @click="searchList"
                   type="primary"
                   size="small">
          查询
        </el-button>
        <el-button style="float: right;margin-right: 15px"
                   @click="reset"
                   size="small">
          重置
        </el-button>
      </div>
      <div style="margin-top: 40px">
        <el-form :inline="true"
                 :model="list"
                 size="small"
                 label-width="140px">
          <el-form-item label="商品名称：">
            <el-input style="width: 203px"
                      v-model="list.goodName"
                      clearable
                      placeholder="商品名称"></el-input>
          </el-form-item>
          <el-form-item label="商品编码：">
            <el-input style="width: 203px"
                      v-model="list.goodId"
                      clearable
                      placeholder="商品编码"></el-input>
          </el-form-item>
          <el-form-item label="规格编码：">
            <el-input style="width: 203px"
                      v-model="list.goodCode"
                      clearable
                      placeholder="规格编码"></el-input>
          </el-form-item>
          <el-form-item label="出售状态：">
            <el-select v-model="list.state" clearable  placeholder="默认为全部">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-tooltip class="item" effect="dark" content="查询【可用库存】数量区间段" placement="bottom">
            <el-form-item label="库存数量：">
              <el-input-number :controls="false" v-model="list.beginStock" step-strictly placeholder="最低"></el-input-number>
              -
              <el-input-number :controls="false" v-model="list.endStock" step-strictly placeholder="最高"></el-input-number>
            </el-form-item>
          </el-tooltip>
          
        </el-form>
      </div>
    </el-card>
    <!-- 数据列表 -->
    <el-card class="operate-container"
             style="margin-top:10px"
             shadow="never">
      <i class="el-icon-tickets"></i>
      <span>数据列表</span>
    </el-card>

    <el-card class="operate-container"
             style="margin-top:10px">
      <el-table :data="tableData"
                style="width: 100%"
                border>
        <el-table-column type="index"
                         width="50"
                         align="center">
        </el-table-column>
        <el-table-column label="平台分类"
                         width="100"
                         align="center">
          <template slot-scope="scope">{{scope.row.categoryName3}}</template>
        </el-table-column>
        <el-table-column label="商品名称"
                         min-width="200"
                         show-overflow-tooltip
                         prop="name"
                         align="center">
        </el-table-column>
        <el-table-column label="出售状态"
                         width="120"
                         align="center">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.salesStatus == 0" type="success">出售中</el-tag>
            <el-tag v-if="scope.row.salesStatus == 1" type="danger">下架</el-tag>
          </template>
        </el-table-column>
        <el-table-column label="商品编码"
                         width="100"
                         prop="productCode"
                         align="center">
        </el-table-column>
        <el-table-column label="规格编码"
                         width="140"
                         align="center">
          <template slot-scope="scope">{{scope.row.skuCode ? scope.row.skuCode : '-'}}</template>
        </el-table-column>
        <el-table-column label="可用库存"
                         width="150"
                         align="center">
          <template slot-scope="scope">
            {{scope.row.usableStock}}
          </template>
        </el-table-column>
        <el-table-column label="库存总数"
                         width="150"
                         prop="totalStock"
                         align="center">
        </el-table-column>
        <el-table-column label="订单占用库存"
                         width="180"
                         prop="orderStock"
                         align="center">
        </el-table-column>
        <el-table-column label="活动锁定库存"
                         width="150"
                         prop="lockStock"
                         align="center">
        </el-table-column>
        <el-table-column label="操作"
                         fixed="right"
                         width="160"
                         align="center">
          <template slot-scope="scope">
            <el-button size="mini"
                       type="success"
                       @click="showProduct(scope.$index, scope.row)">出库
            </el-button>
            <el-button size="mini"
                       type="warning"
                       @click="updateProduct(scope.$index, scope.row)">入库
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>

    <!-- 出库弹窗 -->
    <el-dialog title="出库"
               :visible.sync="dialogFormVisible">
      <el-form :model="outStock"
               label-width="120px">
        <el-form-item label="出库数量：">
          <el-input v-model.number="outStock.number"
                    style="width:200px"></el-input>
        </el-form-item>
        <el-form-item label="出库类型">
          <el-select v-model="outStock.status"
                     placeholder="请选择出库类型：">
            <el-option v-for="item in outStockStatusList"
                       :key="item.value"
                       :label="item.label"
                       :value="item.value"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="备注：">
          <el-input v-model="outStock.remark"
                    type="textarea"
                    maxlength="50"
                    :autosize="{ minRows: 3, maxRows: 4}"
                    style="width:300px"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer"
           class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary"
                   @click="outStockButton">确 定</el-button>
      </div>
    </el-dialog>

    <!-- 入库弹窗 -->
    <el-dialog title="入库"
               :visible.sync="updialogFormVisible">
      <el-form :model="upStock"
               label-width="120px">
        <el-form-item label="入库数量：">
          <el-input v-model.number="upStock.number"
                    style="width:200px"></el-input>
        </el-form-item>
        <el-form-item label="入库类型">
          <el-select v-model="upStock.status"
                     placeholder="请选择入库类型：">
            <el-option v-for="item in upStockStatusList"
                       :key="item.value"
                       :label="item.label"
                       :value="item.value"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="备注：">
          <el-input v-model="upStock.remark"
                    type="textarea"
                    maxlength="50"
                    :autosize="{ minRows: 3, maxRows: 4}"
                    style="width:300px"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer"
           class="dialog-footer">
        <el-button @click="updialogFormVisible = false">取 消</el-button>
        <el-button type="primary"
                   @click="upStockButton">确 定</el-button>
      </div>
    </el-dialog>
    <!-- 固定在下面 -->
    <div class="fixedB">
      <!-- 页码 -->
      <el-pagination background
                     style="margin-top: 10px;margin-left:20px"
                     @size-change="handleSizeChange"
                     @current-change="pageChange"
                     layout="total,sizes,prev,pager,next,jumper"
                     :page-size="page_size"
                     :page-sizes="[15, 30, 50, 100]"
                     :current-page="pageNum"
                     :total="total">
      </el-pagination>
    </div>
  </div>
</template>
<script>
import { fetchList, stockChange } from '@/api/productInventory/productInventory'

export default {
  data() {
    return {
      list: {
        goodName: '',
        goodId: '',
        goodCode: ''
      },
      tableData: [],
      pageNum: 1,
      page_size: 15,
      total: 0,

      options: [
          {
          value: '0',
          label: '出售中'
        },
        {
          value: '1',
          label: '下架'
        },
      ],

      //弹窗
      // 入库选项
      upStockStatusList: [
        {
          value: '0',
          label: '上货入库'
        },
        {
          value: '1',
          label: '退货入库'
        },
        {
          value: '2',
          label: '其他入库'
        },
      ],
      // 出库选项
      outStockStatusList: [
        {
          value: '3',
          label: '销售出库'
        },
        {
          value: '4',
          label: '其他出库'
        }
      ],
      dialogFormVisible: false,
      updialogFormVisible: false,
      outStock: {
        number: '',
        status: '',
        remark: '',
        productId: '',
        skuId: ''
      },
      upStock: {
        number: '',
        status: '',
        remark: '',
        productId: '',
        skuId: ''
      }
    }
  },
  created() {
    if(this.$route.query.beginStock) this.list.beginStock = this.$route.query.beginStock
    if(this.$route.query.endStock) this.list.endStock = this.$route.query.endStock
    this.searchList()
  },
  methods: {
    searchList() {
      fetchList({
        shopId: localStorage.getItem("shopId"),
        name: this.list.goodName,
        productCode: this.list.goodId,
        skuCode: this.list.goodCode,
        salesStatus: this.list.state,
        beginStock: this.list.beginStock || '',
        endStock: this.list.endStock || '',
        pageSize: this.page_size,
        pageNum: this.pageNum,
      }).then(res => {
        this.tableData = res.data.list
        this.total = res.data.total
      })
    },
    // 重置
    reset() {
      this.list = {
        goodName: '',
        goodId: '',
        goodCode: '',
        state: '',
        beginStock:undefined,
        endStock:undefined
      }
      this.searchList()
    },

    //修改每页页数
    handleSizeChange(page) {
      this.page_size = page
      this.searchList()
    },
    //点击跳转页码
    pageChange(page) {
      this.pageNum = page
      this.searchList()
    },

    //出库
    showProduct(index, row) {
      this.dialogFormVisible = true
      this.outStock.productId = row.productId
      this.outStock.skuId = row.code
      this.outStock.total = row.usableStock
    },
    outStockButton() {
      if (typeof (this.outStock.number) != "number") {
        this.$notify.error({
          title: '错误',
          message: '请输入数字'
        });
        return
      }
      if (this.outStock.number <= 0) {
        this.$notify.error({
          title: '错误',
          message: '请输入大于0的值'
        });
        return
      }
      if (this.outStock.number > this.outStock.total) {
        this.$notify.error({
          title: '错误',
          message: '出库量不能大于可用库存量'
        });
        return
      }
      if (this.outStock.status == '') {
        this.$notify.error({
          title: '错误',
          message: '请选择出库类型'
        });
        return
      }
      stockChange({
        shopId: localStorage.getItem("shopId"),
        productId: this.outStock.productId,
        skuId: this.outStock.skuId,
        type: this.outStock.status,
        totalStockChange: -this.outStock.number,
        remark: this.outStock.remark,
        creater: localStorage.getItem("userId")
      }).then(res => {
        if (res.code == 200) {
          this.$notify({
            title: '成功',
            message: '出库成功',
            type: "success"
          });
          this.dialogFormVisible = false
          this.outStock.status = ''
          this.outStock.skuId = ''
          this.outStock.number = ''
          this.outStock.remark = ''
          this.searchList()
        } else {
          this.$notify.error({
            title: '失败',
            message: '出库失败',
          });
        }

      })
    },

    // 入库
    updateProduct(index, row) {
      this.updialogFormVisible = true
      this.upStock.productId = row.productId
      this.upStock.skuId = row.code
    },
    upStockButton() {
      if (typeof (this.upStock.number) != "number") {
        this.$notify.error({
          title: '错误',
          message: '请输入数字'
        });
        return
      }
      if (this.upStock.number <= 0) {
        this.$notify.error({
          title: '错误',
          message: '请输入大于0的值'
        });
        return
      }
      if (this.upStock.status == '') {
        this.$notify.error({
          title: '错误',
          message: '请选择入库类型'
        });
        return
      }
      stockChange({
        shopId: localStorage.getItem("shopId"),
        productId: this.upStock.productId,
        skuId: this.upStock.skuId,
        type: this.upStock.status,
        totalStockChange: this.upStock.number,
        remark: this.upStock.remark,
        creater: localStorage.getItem("userId")
      }).then(res => {
        if (res.code == 200) {
          this.$notify({
            title: '成功',
            message: '入库成功',
            type: "success"
          });
          this.updialogFormVisible = false
          this.upStock.status = ''
          this.upStock.skuId = ''
          this.upStock.number = ''
          this.upStock.remark = ''
          this.searchList()
        } else {
          this.$notify.error({
            title: '失败',
            message: '入库失败',
          });
        }
      })
    }
  }
}
</script>
<style lang="scss" scoped>
.app-container {
  padding: 20px 20px 40px 20px;
}
</style>
